.App {
  text-align: center;
}
.App .resource_container {
  background: #f4f4f4;
}
.App .resource_container .resource_header {
  padding: 0;
  background: none;
  height: 100%;
  line-height: normal;
}
.App .resource_container .resource_main {
  background: #f4f4f4;
  min-height: 800px;
  font-family: "SimSun";
  margin-top: 1rem;
}
.App .resource_container .resource_main > div {
  width: 63%;
  margin: 0 auto;
  height: 100%;
}
.App .resource_container .resource_footer {
  height: 5rem;
  background: #ffffff;
  padding: 0;
  border-top: 1px solid red;
}
.App .compatibility-item {
  background: #fff;
  color: #d80000;
  height: 3rem;
  font-size: 1rem;
  width: 100%;
  padding: 0 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.App .compatibility-item .compatibility-info {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
}
.App .compatibility-item .compatibility-info img {
  margin-right: 0.8rem;
}
.App .compatibility-item .compatibility-handle {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
}
.App .compatibility-item .compatibility-handle img {
  margin-left: 1rem;
}
.App .compatibility-item .compatibility-handle a {
  color: #000;
  margin-left: 3rem;
}
.App .compatibility-item .compatibility-handle a.compatibility-handle_download {
  margin-left: 0.5rem;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}
